.container__main{
width: 85%;
height:90%;
background-color:$containerColor;
position: absolute;
right: 0;
top: 50%;
transform: translateY(-50%);
display: grid;
grid-template-columns: 1fr;
grid-template-rows: 0.4fr 5fr;
grid-row-gap: 10px;
&__topBar{


display: grid;
grid-template-columns: 0.4fr 0.3fr;
justify-content: space-between;
&__search{
    background-color:$searchBarBackground;
    display: flex;
align-items: center;
  padding: 0 0.7rem;
  border-radius: 0.9rem;
    &__input{
        width: 100%;
        height: 100%;
        border: none;
        padding: 0 0.7rem;
        font-size: 1.2rem;
        background-color: $searchBarBackground;
    
    }
}
&__user{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  
    &__p{
        font-size: 1.2rem;
        position: relative;
        font-family:Georgia, 'Times New Roman', Times, serif;
        left: -15px;
    }
    &__svg{
        position: relative;
        left: -30px;
    }
}

}






&__box{
  
    
    display: grid;
    grid-template-columns: 1fr 0.7fr;
    column-gap: 2rem;
grid-column-gap: 1fr;
    &__left{
   
        display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 0.9fr 1.5fr;
   
        &__welcome{
          background-image: url(/assets/4853433.jpg);
          background-position: center;
          background-size: cover;
           background-repeat: no-repeat;
           padding-left: 2rem;
         padding-top: 0.3rem;
         padding-bottom: 0.3rem;
           border-radius: 1.7rem;
           line-height: 1rem;
           display: flex;
           flex-direction: column;
           justify-content: space-evenly;
           &__h1{
              padding-top: 0.7rem;
               color: #f77f00;
               font-size: 1.7rem;
               font-family:$fontLato;
           }
           &__p{
               position: relative;
               color: #a37d31;
               font-family: $fontArvo;
             padding-bottom: 0.6rem;
             &:nth-of-type(2){
                 top: -10px;
             }

              
           }
    &__span{
        position: relative;
        left: -7px;
        display: flex;
        align-items: center;
     font-weight: bolder;
     font-size: 1.5rem;
        &__insideSpan{
         color: rgb(57, 69, 80);
            margin-left: 1rem;
            font-size: 0.8rem;
            font-family: $fontArvo;
            font-weight: bold;
        }
        &:nth-of-type(2){
            left: -4px;
        }
      
    }
        }
        &__georgeHome{
            // background-color: lightgrey;
        }
        &__temperature{
            background-color: magenta;
        }
    }
    &__right{
        background-color: hotpink;
    }
}
}